import React, {Component} from "react";
import NCDatePickerClientS from "../nc_DatePickerTimeZoneConversion";

require("./index.less");

//  
export default class DatePickClientHourTime extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        let Format = "YYYY-MM-DD";
        return (<NCDatePickerClientS
            {...this.props}
            needTime={true}
            needNowTime={true}
            format={Format}
            MultiTZ={true}
            className={`nc_DatePickClientHourTime ${this.props.className}`}
        />);
    }
}

DatePickClientHourTime.defaultProps = {
    showClear: true,
    renderIcon: () => {
        return <i className="icon iconfont">&#xe6cd;</i>;
    }
};
